import m from './main.module.css';
import { NavLink, Link } from 'react-router-dom';
import man from '../../img/5388563362240022086 1.png';
import a from '../../img/icon-metrics-01 1.png';
import b from '../../img/image 12.png';
import c from '../../img/image 13.png';
import d from '../../img/Social Icons.png';
import { useEffect, useState } from 'react';

function Main() {
  const [inAccaunt, setInAccaunt] = useState(false)

  useEffect(() => {
    if(localStorage.getItem("token")){
      setInAccaunt(true)
    }
  }, [])

  const regOrIn = () => {
    if(!inAccaunt){
      return <Link className={m.register} to='/login'> </Link>
    }
    else {
      return <Link className={m.register} to='/myPortfolio'> </Link>
    }
  }

  return (
    <main>
      <section className={m.registration}>
        <div className={m.headers}>
          <h2 className={m.h1}>  </h2>
          <h3 className={m.h2}> , , .  ,   </h3>
          {regOrIn()}
        </div>
        <img className={m.img} src={man} alt="man" />
      </section>
      <section className={m.us}>
        <h3> </h3>
        <p className={m.mission}>
            -        .  
           ,       
            .
        </p>
        <p>
                    
          .     !
        </p>
      </section>
      <section className={m.advantages}>
        <h3></h3>
        <ul className={m.list}>
          <li className={m.item}>
            <img src={a} alt="picture" />
            <h4>  </h4>
          </li>
          <li className={m.item}>
            <img src={b} alt="picture" />
            <h4>    </h4>
          </li>
          <li className={m.item}>
            <img src={c} alt="picture" />
            <h4> </h4>
          </li>
          <li className={m.item}>
            <img src={d} alt="picture" />
            <h4>  </h4>
          </li>
        </ul>
      </section>
      <h2 className={m.achievments}></h2>
    </main>
  );
}

export default Main;
